.container {    }
.container.grid { width: 25%;  }
.container.list { width: 100%; padding: 0.5em; }
.container.grid .video { padding-left: 0.5rem; }
.video {  cursor: pointer; transition: all 0.3s; }
.video:hover { transform: scale(1.02); }
.metadata { margin-left: 0.2em;  }
.container.grid .thumbnail { width: 95%; }
.container.grid .title { margin: 0; width: 95%; font-size: 1rem; font-weight: bold;  word-break: normal; 
    overflow: hidden;  white-space: nowrap;  text-overflow: ellipsis; }
.container.grid .profile { display: flex; margin-top: 1rem; }
.container.grid .profile .channelImg {  width: 2.5rem; height: 2.5rem; border-radius: 50%; }
.container.grid .channel { font-size: 0.9rem; font-weight: normal; margin: 0; padding-left: 0.5rem; color: darkgray; line-height: 2.5rem;  }
.container.grid .statistics { display: flex;  font-size: 0.9rem; color: darkgray; }
.container.grid .count { margin-right: 1rem; }
.container.list .video { display: flex; }
.container.list .thumbnail { flex: 1 1 30%; height: 100px; }
.container.list .metadata { flex: 1 1 70%; }
.container.list .title { margin-left: 0.8rem; margin-top: 0; font-size: 0.8rem; font-weight: bold;  }
.container.list .profile { display: flex; margin-top: 1rem; margin-left: 0.4rem; }
.container.list .profile .channelImg {  width: 2.3rem; height: 2.3rem; border-radius: 50%; }
.container.list .channel { font-size: 0.9rem; font-weight: normal; margin: 0; padding-left: 0.5rem; color: darkgray; line-height: 2.3rem;  }
.container.list .statistics { display: flex; font-size: 0.8rem; margin-left: 0.8rem; margin-top: 0rem; color: darkgray; }
.container.list .count { margin-right: 1rem; }
