.contentleft {  background: #2c2c2c; padding-top: 8rem; height: 100%; }
.contentleft a { display: block; transition: all 0.3s; color: #fff; font-size: 1.3rem; text-align: center; margin-bottom: 4rem; }
.contentleft a:hover { color: rgb(244, 65, 51); }
.contentleft a:hover span { color: #fff; }
.contentleft a span { display: block; font-size: 0.8rem; margin-top: 0.5rem; color: #fff; }